<template>
    <div class="card mb-4">
        <div class="card-body">
            <h5 class="card-title">
                <a :href="link">{{ title }}</a>
            </h5>
            <h6 class="card-subtitle mb-2 text-muted">
                {{ subtitle }}
            </h6>
            <p class="card-text">
                {{ content }}
            </p>
            <div class="text-right">
                <i class="far fa-calendar-alt"></i>
                {{ showTime }}
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        title:{
            type: String,
            required: true
        },
        subtitle:{
            type: String,
            required: false
        },
        content:{
            type: String,
            required: false,
        },
        link:{
            type: String,
            required: true,
            default: '/'
        },
        time:{
            type: Number,
            required: false,
            default: 0
        }
    },
    computed: {
        showTime: {
            get(){
                return new Date(this.time).toLocaleString();
            }
        }
    }
}
</script>


<style scoped>
.text-right{
    text-align:right;
}
</style>