<template>
  <div class="home">
    <page-title>
      <i class="fas fa-2x fa-space-shuttle"></i>
      <i class="fas fa-2x fa-user-astronaut"></i>
      <i class="fas fa-2x fa-satellite"></i>
      <i class="fas fa-2x fa-meteor"></i>
      <i class="fas fa-2x fa-rocket"></i>
      <i class="fas fa-2x fa-globe-americas"></i>
    </page-title>

    <base-post 
      v-for="post in posts" :key="post.id"
      :title="post.title"
      :content="post.content"
      :link="post.url"
      :time="post.time"
    ></base-post>
  </div>
</template>

<script>
// @ is an alias to /src
import BasePost from '@/views/components/ui/BasePost.vue';
import PageTitle from '@/views/components/ui/PageTitle.vue';
import PostService from '@/services/PostService.js';

export default {
  components: { BasePost, PageTitle },
  data(){
    return {
      posts: []
    };
  },
  mounted: function(){
    this.loadPost();
  },
  methods: {
    loadPost :function(){
      PostService.all().then( data => {
        this.posts = data;
      });
    }
  },
  
}
</script>

